.portoflio{
    margin-top: 10%;
    margin-bottom: 10%;
    .CategoryCont{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 5%;
        span{
            font-size: 2rem;
            border-radius: 50%;
            margin: .5em;
            &:hover{
                
                cursor: pointer;
            }
            &:visited{
                background: #78909C ;
            }
            &:active{
                background: #78909C ;
            }
        
            p{
                padding: .5em .7em;
                margin: 0%;
            }
        }
    }
    
    .projectCont{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 5%;
        justify-content: center;
        overflow: hidden;
        height: 100vh;
        .devProject{
            .project{
                display: flex;
                justify-content: center;
                align-items: center;
                box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
                img{
                    width: 100%;
                    height: 260px;
                    transition:  .5s ease-in-out;
                    &:hover{
                        cursor: pointer;
                        filter: blur(8px);
                    }
                }
        
            }
         .ProjectInfo{
             text-align: center;
             display: flex;
             flex-direction: column;
             justify-content: center;
             align-items: center;
             z-index: 1;
             position: absolute;
             color: white;
             background: rgba(0, 0, 0, 0.377);
             width: 40%;
             animation: scal 1s; 
             div{
                  display: flex;
                  flex-direction: column;
                  justify-content: center;
                  align-items: center;
                  width: 90%;  
                  p:nth-child(1){
                      font-weight: bold;
                      font-size: 2rem;
                      margin-bottom: 0%;
                  }
                }
         }
     
        }
    }
}

.projectDetails{
    display: grid;
    grid-template-columns: 20% 80%;
    width: 100% !important;
    justify-content: space-between;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
    img{
        width: 150%;
      
    }
    .projectInfo{
        width: 90%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-end;
        padding-top: 2em;
        overflow: visible;
        position: relative;
        span{
            position: absolute;
            top: -15px;
            right: -14%;
            border: 1px solid rgb(173, 172, 172);
            background: white;
            border-radius: 50%;
            &:hover{
                cursor: pointer;
                background: rgb(173, 172, 172);
            }
            p{
                margin-bottom: 0%;
                padding: .5em 1em;
            }
        }
        section:nth-child(1){
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
        }
        section:nth-child(6){
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            button:nth-child(2){
                border: 2px solid #78909C !important ;
                color: #78909C !important;
            }
        }
        section{
            width: 80%;
            display: flex;
            justify-content: space-between;
            .link{
                color: #5A92CD;
            }
            p:nth-child(1){
                font-weight: bold;
            }
            p:nth-child(2){
                font-weight: 400;
            }
        }
    }
}


.page{
    position: absolute;
    top: 100px;
    left: 0;
    right: 0
  }
  
  .page-enter{
    opacity: 0.01;
    transform: translateX(-100%)
  }
  
  .page-enter-active{
    opacity: 1;
    transform: translateX(0%);
    transition: all 200ms ease-in
  }
  
  .page-exit {
    opacity: 10;
    transform: translateX(0%)
  }
  .page-exit-active {
    opacity: 0.01;
    transform: translateX(100%);
    transition: all 200ms ease-in
  }

  .node-enter {
    opacity: 1;
    transition: opacity 500ms ease-in;
  }
  .node-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 500ms, transform 500ms;
  }
  .node-exit {
    opacity: 1;
  }
  .node-exit-active {
    opacity: 0;
    transform: translateX(-100%);
    transition: all 500ms ease-in-out;
  }

@keyframes scal {
    from{transform: scaleY(0) translateY(-50%)
  }
    to{transform: scaleY(1) translateY(0%)}
}

@media only screen and (max-width: 840px){
     .portoflio{
         margin-top: 20%;
         h1{
             font-size: 2rem;
         }
         .CategoryCont{
             margin-bottom: 2em;
             span{
                 font-size: 1.2rem;
             }
         }
         .projectCont{
             display: grid;
             grid-template-columns: 1fr;
             grid-row-gap: 3%;
             overflow: scroll;
             .devProject{
                .ProjectInfo{
                    width: 100%;
                    div{
                        width: 95%;
                        p{
                            margin-bottom: .5em;
                        }
                    }
                }
             }
           
         }
     }
     .projectDetails{
         display: flex;
         flex-direction: column;
         justify-content: center;
         align-items: center;
         img{
             width: 100%;
         }
         .projectInfo{
            width: 100%;
           justify-content: space-between;
           align-items: center;
           section{
               flex-direction: column;
               justify-content: center;
               align-items: center;
               p:nth-child(1){
                margin-bottom: 0%;;
            }
            p:nth-child(2){
                margin-bottom: 1%;;
            }
           }
           section:nth-child(3){
               width: 100%;
            p:nth-child(2){
               font-size: .9rem;
            }
           }
           section:nth-child(6){
               flex-direction: row;
                width: 100%;
           }
         }
     }
}