
.about{
    padding-left: 0% !important;
    padding-right: 0% !important;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 5%;
    margin-bottom: 5%;
   
    .AboutRow{
        width: 100%;
        display: grid;
        grid-template-columns: 50% 50%;
        justify-content: center;
        align-items: flex-start;
        box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
    }
    
    .aboutme{
        width: 80%;
        text-align: left;
        margin-top: 2%;

        &:first-child{
            margin-left: 2%;
        }
        h2{
            font-weight: bold;
        }
        p{
            font-weight: 500;
            a{
                text-decoration: underline;
                font-weight: bold;
            }
        }
    }
    .animeEnter{
        transform: translateX(0%);
    }
    .basicInfo{
        width: 100%;
        text-align: left;
        margin-top: 2%;
        animation: exit 2s ease-in;
        h2{
            font-weight: bold;
        }
        .myinfo{
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 70%;
            p:nth-child(1){
                font-weight: bold;
            }
            p:nth-child(2){
                font-weight: 500;
            }
        }
    }
}

@keyframes enter{
    from{
        transform: translateX(-100%);
    }
    to{
        transform: translateX(0%);
    }
}
@keyframes exit{
    from{
        transform: translateX(100%);
    }
    to{
        transform: translateX(0%);
    }
}


@media only screen and (max-width: 480px){
    .AboutRow{
       display: flex !important;
       flex-direction: column !important;
       width: 95% !important;
       .aboutme{
           width: 95%;
           &:first-child{
               margin-left: 0%;
               padding-left: 1em;
           }
           
       }
       .basicInfo{
           margin-left: 0%;
           margin-left: 0%;
           padding-left: 1em;
           .myinfo{
               width: 100%;
           }
       }
    }
}