.contactMe{
    width: 50% !important;
    margin-top: 5%;
   
    margin-bottom: 5%;
  .ContactMe-content{
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: 4em;
      margin-top: 2em;
      .contactMe-info{
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: .2fr .2fr .2fr;
        align-items: center;
        justify-content: center;
        text-align: left;
        span{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            p:nth-child(1){
                margin-bottom: 0%;
                font-weight: bold;
            }
            p:nth-child(2){
                font-weight: 500;
                margin-bottom: 0%;
            }
        }
    }
    .forms{
        text-align: left;
        .text-center{
            button{
                border: 2px solid #78909C !important ;
                color: #78909C !important;
            }
        }
        .grey-text{
            .cont-input{
                color: red;
                .md-form{
                    margin-bottom: 0% !important;
                    input{
                        margin-bottom: 0% !important;
                    }
                }
                .errors{
                    margin-top: 0% !important;
                    text-align: center;
                    color: red;
                }
            }
        }
        .success-message{
            text-align: center;
            p{
                font-size: 2rem;
            }
            p:nth-child(1){
                margin-bottom: 0%;
            }
            p:nth-child(2){
                font-size: 3rem;
                color: green;

            }
        }
    }
  }

 
}

@media only screen and (max-width: 480px){
.contactMe{
    width: 90% !important;
    .ContactMe-content{
        display: flex;
        flex-direction: column;
        .contactMe-info{
            grid-template-rows: 2fr 1fr 2fr;
          span{
              justify-content: flex-start;
              align-items: flex-start;
          }
        }
    }

}

.forms{
    .text-center{
        display: flex;
        justify-content: flex-start;
    }
}
}

@media only screen and (max-width: 840px){
    .contactMe{
        width: 90% !important;
    }
}